import React, { Component } from 'react'
import { Table, Button } from 'antd'
import Search from 'components/searchComponent'
import { rolePagedList } from 'services/roleManage-service'
import { checkFields } from 'utils/utils'
import EditModal from './editModal'
import AddModal from './addModal'

const config = [{
    name: '',
    key: 'RoleTypeId',
    type: 'input',
    placeholder: ''
}, {
    name: '	',
    key: 'RoleName',
    type: 'input',
    placeholder: ''
}, {
    name: '',
    key: 'Permis',
    type: 'input',
    placeholder: ''
}, {
    name: '',
    key: 'Remark',
    type: 'input',
    placeholder: ''
}]

const pageInit = {
    pageSize: 20,
    pageIndex: 1,
}

class roleManage extends Component {
    constructor (props) {
        super(props)
        this.state = {
            dataSource: [],
            loading: true,
            editVisible: false,
            searchFields: {
                ...pageInit,
                param: {}
            }
        }
    }
    componentDidMount () {
        this.tableData()
    }
    onStateChange = (obj, fn) => {
        // console.log(obj)
        this.setState(obj, fn && fn())
    }
    onhandleChange = (pagination, filters, sorter) => {
        console.log(pagination, filters, sorter)
    }
    onSearch = (val) => {
        // console.log(checkFields(val))
        let obj = checkFields(val)
        let len = Object.keys(obj).length
        if (len > 0) {
            this.setState({
                searchFields: {
                    ...pageInit,
                    param: {
                        ...obj   
                    }
                },
                loading: true
            }, () => this.tableData())
        } else {
            this.onClear()
        }
    }
    onClear = () => {
        this.setState({
            searchFields: {
                ...pageInit,
                param: {}
            },
            loading: true
        }, () => this.tableData())
    }
    tableData = () => {
        const { searchFields } = this.state
        rolePagedList(searchFields).then(res => {
            // console.log('rolePagedList--->', JSON.parse(res))
            // let dataSource = []
            // for (let i = 0; i < 30; i++) {
            //     dataSource.push({
            //         RoleTypeId: i,
            //         RoleName: i,
            //         Permis: i,
            //         Remark: i
            //     })
            // }
            let { Item1, Item2 } = JSON.parse(res)
            this.setState({
                // dataSource,
                dataSource: Item1,
                total: Item2,
                loading: false
            })
        }).catch(err => {
            this.setState({
                dataSource: [],
                loading: false
            })
        })
    }
    onAddClick = () => {
        this.setState({
            addVisible: true
        })
    }
    editClick = (text, record) => () => {
        // console.log(record)
        this.setState({
            editVisible: true
        })
    }
    render () {
        const columns = [{
            title: '',
            dataIndex: 'RoleTypeId',
            key: 'RoleTypeId'
        }, {
            title: '',
            dataIndex: 'RoleName',
            key: 'RoleName'
        }, {
            title: '',
            dataIndex: 'Permis',
            key: 'Permis'
        }, {
            title: '',
            dataIndex: 'Remark',
            key: 'Remark'
        }, {
            title: '',
            dataIndex: 'edit',
            render: (text, record) => {
                return (<Button onClick={this.editClick(text, record)}></Button>)
            }
        }]
        const {
            dataSource,
            loading,
            total,
            addVisible,
            editVisible
        } = this.state
        const pagination = {
            current: 1,
            pageSize: 10,
            showSizeChanger: true,
            showTotal: (total, range) => ` ${total} `,
            total
        }
        return (
            <div className="roleManage">
                <Search
                    config={config}
                    onSearch={this.onSearch}
                    onClear={this.onClear}
                />
                <div className="mb10">
                    <Button type="primary" icon="plus" onClick={this.onAddClick}></Button>
                </div>
                <Table
                    loading={loading}
                    dataSource={dataSource}
                    columns={columns}
                    pagination={pagination}
                    onChange={this.onhandleChange}
                    bordered
                />
                <AddModal visible={addVisible} onStateChange={this.onStateChange} />
                <EditModal visible={editVisible} onStateChange={this.onStateChange} />
            </div>
        )
    }
}

export default roleManage